


import { MenuData, Factory } from '#/storage'
export default {
name: 'TopMenu',
props: {
current: {
type: String,
default: ''
}
},
data () {
return {
factoryInfo: {},
menuArr: null
}
},
methods: {
toMenu (item) {
switch (item.type) {
case 'home':
if (item.type !== this.current) {
this.$router.push('/')
}
break
case 'newslist':
if (item.type !== this.current) {
this.$router.push({path: '/newslist', query: {classid: item.relateid}})
}
break
case 'productlist':
if (item.type !== this.current) {
this.$router.push({path: '/productlist', query: {classid: item.relateid}})
}
break
case 'aboutus':
if (item.type !== this.current) {
this.$router.push({path: '/aboutus'})
}
break
}
}
},
created () {
this.factoryInfo = Factory.get()
this.menuArr = MenuData.get()
}
}



